  display: block;
  float: left;
}

[dir=rtl] .pnotify-icon, [dir=rtl] .pnotify-icon span {
  float: right;
}

.pnotify-modal-overlay {
  background-color: rgba(0, 0, 0, 0.6);
  top: 0;
  left: 0;
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 2;
  transition: opacity 0.25s linear;
  opacity: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.pnotify-modal-overlay-up {
  align-items: flex-start;
}

.pnotify-modal-overlay-left {
  justify-content: flex-start;
  align-items: center;
}

.pnotify-modal-overlay-right {
  justify-content: flex-end;
  align-items: center;
}

.pnotify-modal-overlay.pnotify-modal-overlay-in {
  opacity: 1;
}

.pnotify-modal-overlay-closes:after {
  content: "×";
  font-family: Arial;
  font-size: 3rem;
  color: #fff;
  text-shadow: 0 0 0.4rem #fff;
}

body > .pnotify-modal-overlay {
  position: fixed;
  z-index: 100041;
}

.pnotify-container {
  background: rgba(0, 0, 0, 0.9);
  border-radius: 10px;
  color: #fff;
}