.profile {

  &__body {
  }

  &__hat {
    display: flex;
    justify-content: space-between;
    padding-bottom: 30px;

    @include width-768 {
      flex-direction: column;
    }
  }

  &__title {
    text-align: left;
    font-size: 40px;
    // max-width: 400px;
    margin: 0;
    // color: #FF4D1C;

    @include width-1200 {
      max-width: 100%;
    }
  }

  & .content-block__more:not(:only-child) {
    margin-top: 0;
  }

  &__btn {
    // max-width: 160px;
    height: fit-content;
    width: fit-content;

    & .content-block-more {
      padding: 15px 25px;
      & svg {
        width: 20px;
        height: 20px;
        margin-left: 10px;
        transition: all 0.3s;
      }
      &:hover {
        // background: #FF4D1C;
        & svg {
          fill: #ffffff;
          transition: all 0.3s;
        }
      }
    }
    &-exit {
      margin-left: 40px;

      @include width-768 {
        margin-left: 0;
        &.content-block__more:not(:only-child) {
          margin-top: 30px;
        }
      }
      & .content-block-more {
        padding: 15px 30px;
        display: flex;
        align-items: center;
        & span {
          font-size: 18px;
          font-weight: 400;
        }
      }
    }
  }

  &__tabs {
    margin-top: 30px;
  }

  & .tabs .tabs__nav-btn--active {
    background: #040404;
    color: #ffffff;
    scale: 1.06;
    & svg {
      fill: #ffffff;
      transition: all 0.3s;
    }
  }

  &__content {
    background: #f7f7f7;
    padding: 50px 40px;
    border-radius: 15px;
    margin-top: 30px;

    @include width-768 {
      padding: 30px 20px;
    }
  }
  &__subtitle {
    font-size: 30px;
    font-weight: 500;
    line-height: 100%;
    color: #333333;
    // text-align: center;
    & span {
      color: #FF4D1C;
    }
    @include width-1200 {
      font-size: 25px;
    }
    @include width-768 {
      font-size: 20px;
    }
  }
  &__text {
    display: block;
    font-size: 18px;
    font-weight: 400;
    line-height: 130%;
    color: #333333;
    max-width: 700px;
    // margin: 0 auto;
    margin-top: 30px;
    & p {
      font-size: 18px;
      &:not(:last-child) {
        margin-bottom: 20px;
      }
      & b {
        & span {
          color: #FF4D1C;
        }
      }
    }

    @include width-1200 {
      font-size: 16px;
      margin-top: 20px;
      & p {
        font-size: 16px;
      }
    }
    @include width-768 {
      font-size: 14px;
      & p {
        font-size: 14px;
      }
    }
  }
  & .info__item:first-child {
    margin-top: 30px;
    padding-top: 0;
    margin-bottom: 20px;
  }
  & input, textarea {
    background-color: #ffffff !important;
    box-shadow: inset 0 0 0 2px #e0e0e0;
  }
  & textarea {
    min-height: 200px;
  }

  &-password {
    max-width: 600px;
    // margin: 0 auto;
    &__btn {
      background: #ffffff;
      max-width: 300px;
      // margin: 0 auto;
      @include width-576 {
        max-width: 100%
      }
    }
    & .info__item {
      margin-top: 30px;
    }
  }

  &-ask {
    &__success {
      margin-top: 50px;
      background: #39ac08;
      padding: 20px;
      border-radius: 20px;
      & .profile__subtitle {
        color: #ffffff;
      }
      & .profile__text {
        color: #ffffff;
        margin-top: 20px;
      }
    }
  }

  &-test {
    &__btn {
      &.content-block__more:not(:only-child) {
        margin-top: 30px;
      }
    }
  }
}

.tabs {
  @include tabs();
  &__nav {
    list-style: none;
    padding-left: 0;
    display: flex;
    margin-bottom: 0;
    margin-left: -20px;
    padding-left: 20px;
    margin-right: -20px;
    padding-right: 20px;

    @include width-1200 {
      overflow-x: scroll;
      /* Hide scrollbar for Chrome, Safari and Opera */
      &::-webkit-scrollbar {
        display: none;
      }

      /* Hide scrollbar for IE, Edge and Firefox */
      & {
        -ms-overflow-style: none;  /* IE and Edge */
        scrollbar-width: none;  /* Firefox */
      }
    }
  }

  &__nav-item {
    max-width: 100%;
    width: fit-content;
    flex-shrink: 0;
    &:not(:last-child) {
      margin-right: 20px;

      @include width-1200 {
        margin-right: 10px;
      }
    }
  }

  &__nav-btn {
    // padding: 15px 40px;
    font-size: 16px;
  }

  &__content {
  }

  &__panel {
  }
}

.step {
  font-size: 30px;
  line-height: 100%;
  font-weight: 500;
  text-align: center;

  @include width-768 {
    font-size: 25px;
  }
  &__container {
    padding-bottom: 0;
    padding-top: 50px;
  }
  & span {
    color: #FF4D1C;
  }
}
