.test {
  margin-top: 50px;
  counter-reset: myCounter;
  display: grid;
  grid-template-columns: 1fr 250px;
  gap: 80px;
  position: relative;
  &.hide {
    display: none;
  }

  @include width-1200 {
    gap: 40px;
  }
  @include width-960 {
    grid-template-columns: 1fr;
    margin-bottom: 60px;
  }
  &__checkbox {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__subtitle {
    font-size: 20px;
    font-weight: 500;
    color: #333333;

    @include width-960 {
      font-size: 18px;
    }
    & span {
      position: relative;
      &::before {
        counter-increment: myCounter;
        content: counter(myCounter) '.';
        margin-right: 15px;
      }
    }
  }

  & label {
    color: #333333;

    @include width-960 {
      font-size: 14px;
    }
  }

  & .info__item {
    &:first-child {
      padding-top: 0;
    }
  }

  & .info__item {
    margin-top: 50px;

    @include width-960 {
      margin-top: 40px;
    }
  }

  // & .info__item:last-child {
  //   padding: 50px 40px;

  //   @include width-768 {
  //     padding: 30px 20px;
  //   }
  // }

  & .checkbox__text:before {
    // background: #ffffff;
    // border: 1px solid #818181;
  }

  &-aside {
    text-align: center;
    position: sticky;
    top: 80px;
    height: fit-content;
    z-index: 1000;

    @include width-960 {
      position: fixed;
      bottom: 0;
      left: 0;
      top: auto;
      width: 100%;
    }
    & span {
      font-size: 18px;
      line-height: 100%;

      @include width-960 {
        font-size: 16px;
      }
      @include width-420 {
        font-size: 14px;
      }
    }

    &__timer {
      display: flex;
      flex-direction: column;
      align-items: center;
      flex-shrink: 0;
      & >:last-child {
        display: block;
        margin-top: 10px;
        font-size: 26px;
      }

      @include width-960 {
        flex-direction: row;
        align-items: center;
        & >:last-child {
          margin-left: 20px;
          margin-top: 0;
          font-size: 22px;
        }
      }
      @include width-768 {
        flex-direction: column;
        align-items: flex-start;
        & >:last-child {
          margin-left: 0;
          margin-top: 10px;
          align-self: center;
        }
      }
      @include width-420 {
        flex-direction: row;
        align-items: center;
        & >:last-child {
          margin-top: 0;
          margin-left: 10px;
          font-size: 20px;
        }
      }
    }

    & .profile__content {
      padding: 30px 20px;

      @include width-960 {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 15px;
        box-shadow: 0px 6px 30px 0px #cccccc;
      }
      @include width-768 {
        padding: 10px;
      }
      @include width-420 {
        flex-direction: column;
      }
    }
    & .content-block__more {
      background: #ffffff;
      height: 60px;
      margin-top: 40px;

      @include width-960 {
        max-width: 200px;
        margin-top: 0;
        background: #212529;
        color: #ffffff;
        margin-left: 20px;
      }
      @include width-420 {
        margin-top: 10px;
        height: 50px;
        max-width: 100%;
        margin-left: 0;
      }
    }
  }
  &__end {
    display: none;
    &.active {
      display: block;
    }
    &-btn {
      height: 80px;
      max-width: 300px;
      margin-top: 50px;
      background-color: #ffffff;
    }
  }
}
